<template>
   <div class="mine">
       <Head :data='Title'></Head>
       <div class='mine_center'>
         <div class='cen_top'>
            <div class='user'>
               <div class='head'>
                  <img :src="userInfo.avatar" alt="">
               </div>
               <div class='username'>
                  <p>{{userInfo.nickName}}</p>
                  <p class='realName' @click='realName()'>完成实名</p>
               </div>
            </div>      
            <div class='user_bot'>
               <div class='gold' style='border-right:3px solid whitesmoke;'>
                  <router-link to='/sellbill'>我的卖单</router-link>
               </div>
               <div class='gold'>    
                  <router-link to='/buybill'>我的买单</router-link>
               </div>
            </div>  
         </div>
         <div class='cen_wallet'>    
            <div v-for='(item,index) in arr' :key='index' @click='skip(index)'>
                  <div class='xtoken_lt'>
                     <img :src="item.img" alt="">
                  </div>
                  <div class='xtoken_rt'>
                     <p>{{item.title}}</p>
                     <span></span>
                  </div>
            </div>
         </div>
         <div class='cen_bot'>
            <p @click='exit()'>退出登录</p>
         </div>
       </div>
       <Tab :Index='Index'></Tab>
   </div>
</template>

<script>

import Tab from '../components/Tab'
import Head from '../components/Head'

export default {
   name: 'mine',
   components:{ Tab,Head },
   data() {
       return {
          userInfo:{
            nickName:localStorage.nickName,
            avatar:localStorage.avatar
          },
          Title:'个人中心',
          arr:[
             { title:'钱包地址' , path:'/wallet' , img:require('../../static/images/background/Wallet.png')},
             { title:'交易订单' , path:'/pay' , img:require('../../static/images/background/dealbill.png')},
             { title:'支付信息' , path:'/dealbill' , img:require('../../static/images/background/paymsg.png')},
             { title:'提币地址' , path:'/walletsite' , img:require('../../static/images/background/walletsite.png')},
             { title:'设置' , path:'/set' , img:require('../../static/images/background/set.png')},
             { title:'帮助' , path:'/help' , img:require('../../static/images/background/help.png')},
          ],
          showIndex:0,
          Index:4
       }
   },
   methods:{
      exit(){
         let url = '/userLogin/logout';
         this.$http.post(url,{
            cmd:'102',
            body:{
               accessToken:localStorage.token,
               did:'device id'
            }
         }).then( (res) => {
            // console.log(res);
            localStorage.clear();
            this.$router.push('/login');
         })
      },
      skip(index){      
         let p = this.arr[index].path;
         this.$router.push({path:p});
      },
      realName(){
         this.$router.push('/idcation');
      }
   }
}
</script>

